
h1 {
  font-size: 2.5em;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  max-width: 700px;
  margin-top: 40px;
  margin-bottom: 60px;
}
#livecheck {
  font-size: 1.5em;
  color: 	#23d160;
}
#title {
  margin-bottom: 20px;
}
#success {
  margin-top: 50px;
}
.center {
  text-align: center;
}
.required-field > label::after {
  content: '*';
  color: red;
  margin-left: 0.25rem;
}
.svg-logo {
  margin-bottom: 30px;
}
.fa-check {
  color: #23d160;
}
/* Too much red with this on but leaving it here to show I considered it. 
.fa-exclamation-triangle {
  color: red;
} */
@media (max-width: 767px) {
#app {
    padding: 0 40px;
}
}
@media (max-width: 568px) {
h1 { 
    font-size: 1.6em;
    font-weight: bold;
}
}
